//      
export const LOGIN = 'LOGIN';
export const LOGOUT = 'LOGOUT';

//   
export const login = (userData) => ({
    type: LOGIN,
    payload: userData,
});

export const logout = () => ({
    type: LOGOUT,
});

//  
const initialState = {
    isLoggedIn: false,
    userData: null,
};

// 
const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case LOGIN:
            return {
                isLoggedIn: true,
                userData: action.payload,
            };
        case LOGOUT:
            return {
                isLoggedIn: false,
                userData: null,
            };
        default:
            return state;
    }
};

